/* eslint-disable vue/require-prop-types */
export default {
  name: 'TabBottom',

  props: ['tabs'],

  computed: {
    activeTab () {
      return this.findactiveTab()
    }
  },

  methods: {
    findactiveTab () {
      let position = 0
      this.tabs.forEach((element, index, self) => {
        if (element.active) {
          position = index
        }
      })
      return position
    }
  }
}
